import React, { useState, useEffect }from 'react'
import { Form, Input, message } from 'antd';
import { Link, useNavigate } from 'react-router-dom';
import '../resources/authentication.css'
import axios from 'axios';
import Spinner from '../components/Spinner';

import imagelogin from '../assets/images.jpeg'
function Register() {
    const [loading, setLoading] = useState(false); //react hook for state management
    const navigate = useNavigate();
    const onFinish = async (values) => {
        try {
            setLoading(true);
            const result = await axios.post('/api/users/register', values);
            // console.log(result);
            setLoading(false);
            message.success(result.data);
            navigate('/')
        } catch (error) {
            setLoading(false);
            message.error(error.response.data);
        }
    }
    useEffect(() => {
        if(localStorage.getItem('income-expense-manager-user')){   
            navigate('/');     
        }
    });
  return (
    <div className="register">
        {loading && <Spinner/>}    {/*if both are true the componenet will be launched here!!. */}
        <div className="d-flex justify-content-center pt-5 ">
        <h1>PERSONAL FINANCE TRACKER</h1>
        </div>
        <div className="row reg">
            <div className="col-md-3">            
                <Form layout='vertical' onFinish={onFinish}>
                    <h3>Register</h3>
                    <Form.Item sx={{color:'white'}} label='Name' name="name" rules={[{required: true, message: 'Please enter your name!' },]}>
                        <Input />
                    </Form.Item>                    
                    <Form.Item label='Email' name="email" rules={[{required: true, message: 'Please enter your email!' },]}>
                        <Input />
                    </Form.Item>
                    <Form.Item label='Password'name="password" rules={[{required: true, message: 'Please enter your password!' },]}>
                        <Input.Password />
                    </Form.Item>
                    <div className="d-flex justify-content-between align-items-center">
                        <Link to='/login'>Already Register? Click Here to Login</Link>
                        <button className='primary' type='submit'>Register</button>
                    </div>
                </Form>
            </div>
            <div className="col-md-3"><br/><br/><img src={imagelogin} width="100%" alt="imageatsignup"/>
            </div>
        </div>
    </div>
  )
  
}
export default Register